import React from "react";
import {
  Typography,
  Accordion,
  AccordionDetails,
  AccordionSummary,
} from "@material-ui/core";
import { ExpandMore } from "@material-ui/icons";
// Styles
import { useStyles } from "./ItemStyles";

const Y2015 = () => {
  const classes = useStyles();
  return (
    <Accordion>
      <AccordionSummary
        expandIcon={<ExpandMore />}
        aria-controls="panel1bh-content"
        id="panel1bh-header"
      >
        <Typography className={classes.heading}>Intern</Typography>
        {/* <Typography className={classes.secondaryHeading}>
        I am an accordion
      </Typography> */}
      </AccordionSummary>
      <AccordionDetails>
        <ul>
          <li>
            <Typography>
              Work with Senior Software Engineer throughout the project and
              report project status to Senior Software Engineer
            </Typography>
          </li>
          <li>
            <Typography>
              Assist Senior Software Engineer in software design and coding
            </Typography>
          </li>
          <li>
            <Typography>
              Implemented java code from project design artifacts (UML).
            </Typography>
          </li>
          <li>
            <Typography>
              Implemented java code from project design artifacts (UML).
            </Typography>
          </li>
          <li>
            <Typography>
              Document Unit test cases, integration test cases
            </Typography>
          </li>
          <li>
            <Typography>
              Developed Unit tests using Junit framework in Eclipse IDE 4.0
            </Typography>
          </li>
          <li>
            <Typography>
              Support, maintain, and document software functionality and project
              requirements
            </Typography>
          </li>
        </ul>
      </AccordionDetails>
    </Accordion>
  );
};

export default Y2015;
