import React, { useState } from "react";
// Material UI
import { Tabs, Tab, Divider, Grow, Paper, Typography } from "@material-ui/core";
import {
  Timeline,
  TimelineConnector,
  TimelineContent,
  TimelineDot,
  TimelineItem,
  TimelineOppositeContent,
  TimelineSeparator,
} from "@material-ui/lab";
import AssignmentTurnedInIcon from "@material-ui/icons/AssignmentTurnedIn";
// components
import Y2015 from "./tabs/2015";
import Y2016 from "./tabs/2016";
import ItemTwo from "./tabs/ItemTwo";
import ItemThree from "./tabs/ItemThree";

const Work = () => {
  const styles = {
    timeLinedot: { color: "#fff", backgroundColor: "green" },
  };

  const timeLineItems = [
    {
      year: 2015,
      component: <Y2015 />,
    },
    {
      year: 2016,
      component: <Y2016 />,
    },
  ];

  return (
    <Grow in={true} style={{ transformOrigin: "0 0 0" }} timeout={2000}>
      <Paper
        style={{
          flexGrow: 1,
          display: "flex",
          // margin: "-60px 30px 0px"
          margin: "-61px 30px 0px",
          boxShadow:
            "0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2)",
        }}
      >
        <Timeline align="alternate">
          {timeLineItems.map((timeLineItem) => {
            return (
              <TimelineItem>
                <TimelineOppositeContent>
                  <Typography variant="body2" color="textSecondary">
                    {timeLineItem.year}
                  </Typography>
                </TimelineOppositeContent>
                <TimelineSeparator>
                  <TimelineDot style={styles.timeLinedot}>
                    <AssignmentTurnedInIcon />
                  </TimelineDot>
                  <TimelineConnector />
                </TimelineSeparator>
                <TimelineContent style={{ textAlign: "left" }}>
                  {timeLineItem.component}
                </TimelineContent>
              </TimelineItem>
            );
          })}
          {/* <TimelineItem>
            <TimelineOppositeContent>
              <Typography variant="body2" color="textSecondary">
                2015
              </Typography>
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot style={styles.timeLinedot}>
                <AssignmentTurnedInIcon />
              </TimelineDot>
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
              <Y2015 />
            </TimelineContent>
          </TimelineItem>
          <TimelineItem>
            <TimelineOppositeContent>
              <Typography variant="body2" color="textSecondary">
                2016
              </Typography>
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot style={styles.timeLinedot}>
                <AssignmentTurnedInIcon />
              </TimelineDot>
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
              <Y2016 />
            </TimelineContent>
          </TimelineItem> */}
        </Timeline>
        {/* <Tabs
          orientation="vertical"
          indicatorColor="primary"
          variant="scrollable"
          value={value}
          onChange={handleChange}
          aria-label="Work Vertial Tabs"
        >
          <Tab label="Item 1" />
          <Tab label="Item 2" />
          <Tab label="Item 3" />
          <Tab label="Item 4" />
        </Tabs>
        <Divider orientation="vertical" flexItem />
        {value === 0 && <Y2015 />}
        {value === 1 && <ItemTwo />}
        {value === 2 && <ItemThree />} */}
      </Paper>
    </Grow>
  );
};

export default Work;
