import React, { useState, useRef, useEffect } from "react";
import { Grid, Paper, Typography, Zoom } from "@material-ui/core";
import ProfileImg from "../../asset/profile-pic.jpg";
// TypedJs
import Typed from "typed.js";
// Images
import JavaLogo from "../../asset/java.svg";
import JsLogo from "../../asset/javascript.svg";
import TsLogo from "../../asset/typescript.svg";
// import MsSqlLogo from "../../asset/microsoft-sql-server.svg";
import ReactLogo from "../../asset/react.svg";
import AngularLogo from "../../asset/angular.svg";
import NodeJsLogo from "../../asset/nodejs.svg";
import SpringLogo from "../../asset/spring.svg";

const Home = () => {
  const [isShown, setIsShown] = useState(false);
  const typeNameTarget = useRef(null);
  const typeTitleTarget = useRef(null);
  const typeContentTarget = useRef(null);

  useEffect(() => {
    const typedName = new Typed(typeNameTarget.current, {
      strings: ["Tue Tran"],
      typeSpeed: 30,
      showCursor: false,
      startDelay: 2000,
    });
    const typedTitle = new Typed(typeTitleTarget.current, {
      strings: ["Software Engineer"],
      typeSpeed: 30,
      showCursor: false,
      startDelay: 2500,
    });
    const typedContent = new Typed(typeContentTarget.current, {
      strings: [
        "Based in Bentonville, AR specializing in developing front-end and back-end applications.",
      ],
      typeSpeed: 30,
      showCursor: false,
      startDelay: 3300,
    });
    setTimeout(() => {
      setIsShown(true);
    }, 7500);
    return () => {
      typedName.destroy();
      typedTitle.destroy();
      typedContent.destroy();
    };
  }, []);

  return (
    <Zoom in={true} timeout={2000}>
      <Paper
        elevation={3}
        style={{
          display: "flex",
          alignItems: "center",
          flexDirection: "column",
          margin: "-61px 30px 0px",
          boxShadow:
            "0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2)",
        }}
      >
        <img
          alt="Profile Pic"
          src={ProfileImg}
          style={{ borderRadius: "50%", transform: "translate3d(0, -50%, 0)" }}
        />
        <br />
        <div style={{ marginTop: "-125px", textAlign: "center" }}>
          <Typography
            variant="h3"
            style={{
              color: "#3C4858",
              fontWeight: 700,
            }}
            ref={typeNameTarget}
          ></Typography>
          <Typography
            variant="h5"
            style={{
              color: "#3C4858",
              fontWeight: 500,
            }}
            ref={typeTitleTarget}
          ></Typography>
          <Typography
            variant="h6"
            style={{
              color: "#999",
              fontWeight: 500,
            }}
            ref={typeContentTarget}
          ></Typography>
          {isShown && (
            <Zoom in={true} timeout={2000}>
              <Grid container spacing={1}>
                {/* <Grid item xs={12}>
                  <h1>Technologies</h1>
                </Grid> */}
                <Grid item xs={12} sm={6} md={4}>
                  <h1>Languages</h1>
                  <Grid container spacing={1} alignItems="center" justify="center">
                    <Grid item xs={12} sm={6} md={4}>
                      <img alt="Java Logo" src={JavaLogo} style={{ maxWidth: "50%" }} />
                    </Grid>
                    <Grid item xs={12} sm={6} md={4}>
                      <img alt="JS Logo"  src={JsLogo} style={{ maxWidth: "50%" }} />
                    </Grid>
                    <Grid item xs={12} sm={6} md={4}>
                      <img alt="TS Logo"  src={TsLogo} style={{ maxWidth: "50%" }} />
                    </Grid>
                  </Grid>
                </Grid>
                <Grid item xs={12} sm={6} md={4}>
                  <h1>Front End</h1>
                  <Grid container spacing={1} alignItems="center" justify="center">
                    <Grid item xs={12} sm={6} md={4}>
                      <img alt="React Logo"  src={ReactLogo} style={{ maxWidth: "50%" }} />
                    </Grid>
                    <Grid item xs={12} sm={6} md={4}>
                      <img alt="Angular Logo"  src={AngularLogo} style={{ maxWidth: "50%" }} />
                    </Grid>
                  </Grid>
                </Grid>
                <Grid item xs={12} sm={6} md={4}>
                  <h1>Back End</h1>
                  <Grid container spacing={1} alignItems="center" justify="center">
                    <Grid item xs={12} sm={6} md={4}>
                      <img alt="NodeJs Logo"  src={NodeJsLogo} style={{ maxWidth: "50%" }} />
                    </Grid>
                    <Grid item xs={12} sm={6} md={4}>
                      <img alt="Spring Logo"  src={SpringLogo} style={{ maxWidth: "50%" }} />
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            </Zoom>
          )}
        </div>
      </Paper>
    </Zoom>
  );
};

export default Home;
